import React from 'react';

import {
  WrapperHeader,
  WrapperAdd,
} from './styles';

import { 
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  ListItemText,
  ListItemAvatar,
  TextField,
  Avatar,
} from '@material-ui/core';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import PersonIcon from '@material-ui/icons/Person';

import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';
import { atualiza } from '../../utils/Services';

import SaveIcon from '@material-ui/icons/Save';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Valida } from '../../utils/Validando';
import InputMask from 'react-input-mask';

import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import FormHelperText from '@material-ui/core/FormHelperText';
import Switch from '@material-ui/core/Switch';

const useStyles = makeStyles((theme) => ({
  formControl: {
    minWidth: '100%',
  }
}));

const Conta = () => {
  
  const classes = useStyles();
  const [accountName, setAccountName, accountNameInicial] = useGenericModel('account_name');
  const [openALert, closeAlert, Alert] = useSnackbarsAlert()
  const [openBackdro, closeBackdro, Backdro] = useBackdrop()
  const [changeform, setChangeform] = React.useState(false)

  const [input, setInput] = React.useState({
    nickname: {value: "", error: false, valido: false},
    modelo: {value: "", error: false, valido: false},
    cor_principal: {value: "", error: false, valido: false},
    cor_segundaria: {value: "", error: false, valido: false},
    bio: {value: "", error: false, valido: false},
    galeria: {value: "", error: false, valido: false},
    servico: {value: "", error: false, valido: false},
  })

  const theme = useTheme();

  React.useEffect(() => {
    setInput({
      nickname: {value: accountName?.account_name ? accountName?.account_name : "", error: false, valido: true},
      modelo: {value: accountName?.modelo ? accountName?.modelo : "", error: false, valido: true},
      cor_principal: {value: accountName?.cor_principal ? accountName?.cor_principal : "", error: false, valido: true},
      cor_segundaria: {value: accountName?.cor_segundaria ? accountName?.cor_segundaria : "", error: false, valido: true},
      bio: {value: accountName?.bio ? accountName?.bio : "", error: false, valido: true},
      galeria: {value: accountName?.galeria ? accountName?.galeria : "", error: false, valido: true},
      servico: {value: accountName?.servico ? accountName?.servico : "", error: false, valido: true},
    })
  },[accountName]);


  const  lidandoForm = (event, validate) => {
      setChangeform(true)
      let valida  = Valida[validate]
      if(valida){
          let result = valida(event.target.value, event.target.name, event.target.required)
          if (result.status){
              setInput({...input, [event.target.name]: {value: result.value, error: false, valido: true}})
          }else{
              setInput({...input, [event.target.name]: {value: result.value, error: result.msg, valido: false}})
          }
      }else{
        setInput({...input, [event.target.name]: {value: event.target.value, error: false, valido: true}})
      }
  }

  const handleChange = (event) => {
    setInput({ ...input, [event.target.name]: {value: event.target.checked, error: false, valido: true}});
  };

  async function enviaFormularioAccount(){
    if( Valida.formEhValido(input) ){
        openBackdro()
        let novoInput = {...accountName}
        novoInput.modelo = input?.modelo.value
        novoInput.cor_principal = input?.cor_principal.value
        novoInput.cor_segundaria = input?.cor_segundaria.value
        novoInput.bio = input?.bio.value
        novoInput.galeria = input?.galeria.value
        novoInput.servico = input?.servico.value

        let result = await atualiza('account_name', novoInput)
        setTimeout(() => {
          if ( result?.id ) {
              setAccountName(result)
              openALert({
                msg: "Solicitao realizada!",
                severity: "success"
              })
          }else{
              openALert({
                msg: "Solicitao no pode ser concluida!",
                severity: "error"
              })
          }
          closeBackdro()
        }, 300);
    }
  }

  return (
      <div>  
          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Nickname" secondary={ input?.nickname.value ? input?.nickname.value : "" } />
                    </AccordionSummary>
                    <AccordionDetails>
                            <TextField 
                              fullWidth 
                              id="input-nome-da-conta" 
                              label="nickname"
                              variant="outlined" 
                              size="small"
                              name="nickname"
                              value={input?.nickname.value ? input?.nickname.value : ""}
                              onChange={ (event) => lidandoForm(event, 'nickname') }  
                              helperText={input?.nickname?.error ? input?.nickname.error : ""}
                              error={input?.nickname?.error ? true : false}
                            />
                    </AccordionDetails>
          </Accordion>

          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Modelo" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                    <FormControl 
                          className={classes.formControl} 
                          variant="outlined" 
                          style={{ margin: '5px 0 8px 0' }}
                          >
                          {/* <InputLabel htmlFor="age-native-simple">Modelo</InputLabel> */}
                              <Select
                                      native
                                      value={input.modelo?.value}
                                      onChange={ (event) => lidandoForm(event, false)}
                                      inputProps={{
                                          name: 'modelo',
                                          id: 'tipo-de-servico',
                                      }}
                                      >
                                      <option value={0}>Carto</option>
                                      <option value={1}>Identidade</option>
                              </Select>
                          <FormHelperText>Modalidade do servio</FormHelperText>
                      </FormControl>
                    </AccordionDetails>
          </Accordion>

          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Cor principal" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                        <input 
                          type="color" 
                          id="id_cor_principal" 
                          name="cor_principal" 
                          value={input.cor_principal?.value}
                          onChange={ (event) => lidandoForm(event, false) }
                          />
                    </AccordionDetails>
          </Accordion>

          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Cor segundria" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                        <input 
                          type="color" 
                          id="id_cor_segundaria" 
                          name="cor_segundaria" 
                          value={input.cor_segundaria?.value}
                          onChange={ (event) => lidandoForm(event, false) }
                          />
                    </AccordionDetails>
          </Accordion>

          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Servios" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                      <Switch
                        checked={input.servico?.value}
                        onChange={handleChange}
                        color="primary"
                        name="servico"
                        inputProps={{ 'aria-label': 'primary checkbox' }}
                      />
                    </AccordionDetails>
          </Accordion>
          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Biografia" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                      <Switch
                        checked={input.bio?.value}
                        onChange={handleChange}
                        color="primary"
                        name="bio"
                        inputProps={{ 'aria-label': 'primary checkbox' }}
                      />
                    </AccordionDetails>
          </Accordion>
          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Galeria" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                      <Switch
                        checked={input.galeria?.value}
                        onChange={handleChange}
                        color="primary"
                        name="galeria"
                        inputProps={{ 'aria-label': 'primary checkbox' }}
                      />
                    </AccordionDetails>
          </Accordion>

          <Zoom
            key="primary"
            in={1}
            timeout={{
              enter: theme.transitions.duration.enteringScreen,
              exit: theme.transitions.duration.leavingScreen,
            }}
            style={{
              transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
            }}
            unmountOnExit
          >
            <Fab
              onClick={ Valida.formEhValido(input) && changeform ? enviaFormularioAccount : () => {} }
              aria-label="addicon"
              disabled={ Valida.formEhValido(input) && changeform ? false : true }  
              color="primary"
              style={{
                position: 'fixed',
                bottom: '30px',
                right: '30px',
                backgroundImage: Valida.formEhValido(input) && changeform ? 'linear-gradient(to right, #8f00dd , #6866ff)' : 'none',
              }}
            >
              <SaveIcon />
            </Fab>
          </Zoom>

        <Alert />
        <Backdro /> 
       </div>
  );
}

export { Conta }