import React from 'react';
import TextField from '@material-ui/core/TextField';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';

import {
    WrapperUpload,
    ButtonUploadBox,
} from '../styles';

import { Valida } from '../../../utils/Validando';

const FormularioServicoSobre = ({
    nome,
    setNome,
    descricao,
    setDescricao,
    img,
    setImg,
    isvalid
}) => {

    const hiddenFileInput = React.useRef(null);
    const [file, setFile] = React.useState(null);

    const handleClick = event => {
      hiddenFileInput.current.click();
    };

    const handleChangeFile = (event) => {
        setFile(URL.createObjectURL(event.target.files[0]))
        setImg(event.target.files[0])
    }

    React.useEffect( ()=>{
        if(img){
            setFile(URL.createObjectURL(img))
        }
        if( nome.valido && descricao.valido ){
            isvalid.setPasso({...isvalid.passo, [1]: true})
        }else{
            isvalid.setPasso({...isvalid.passo, [1]: false})
        }
    },[nome, descricao])

    const lidandoForm = (event, validate, set) => {
        let valida  = Valida[validate]
        if(valida){
            let result = valida(event.target.value, event.target.name, event.target.required)
            if (result.status){
                set({value: result.value, error: false, valido: true})
            }else{
                set({value: result.value, error: result.msg, valido: false})
            }
        }
    }

    return (
        <>
            <TextField
            id="descricao-nome"
            label="Nome"
            style={{ margin: '5px 0 10px 0' }}
            placeholder=""
            fullWidth
            required
            margin="normal"
            variant="outlined"
            InputLabelProps={{
                shrink: true,
            }}
            name="nome"
            value={nome?.value}
            onChange={ (event) => lidandoForm(event, 'text', setNome) }
            helperText={nome?.error ? nome.error : "Nome do servio"}
            error={nome?.error ? true : false}
            />
            <TextField
            id="descricao-id"
            label="Descrio"
            style={{ margin:  '5px 0 0 0' }}
            multiline
            fullWidth
            margin="normal"
            variant="outlined"
            InputLabelProps={{
                 shrink: true,
            }}
            name="descricao"
            required
            value={descricao?.value}
            onChange={ (event) => lidandoForm(event, 'text', setDescricao) }
            helperText={descricao?.error ? descricao.error : "Descrio do servio"}
            error={descricao?.error ? true : false}
        />

        <FormControl>
            <WrapperUpload>
                <ButtonUploadBox onClick={handleClick} src={file} >
                    { file ? <></> :  <AddPhotoAlternateIcon /> }
                </ButtonUploadBox>
                <input type="file"
                        onChange={handleChangeFile}
                        ref={hiddenFileInput}
                        style={{display:'none'}} 
                /> 
            </WrapperUpload> 
            <FormHelperText>Imagem de referncia</FormHelperText>
        </FormControl>

    </>
    )

}

export { FormularioServicoSobre } 