import React from 'react';

import {
    ContainerModal,
    CardServico, 
    useStyles,
    WrapperCardMedia,
    WrapperCalendario,
    WrapperInfosAgenda,
    InfosAgenda,
    WrapperFormDia,
    WrapperButtonOption,
    WrapperCardAvatar,
    WrapperCardinfo,
    CardInfo,
    WrapperCardAction,
} from './styles';

import {
    Avatar,
    CardContent,
    CardMedia,
    Typography,
    ListItem,
    ListItemText,
    ListItemAvatar,
    Divider,
    FormControl,
    Select,
} from '@material-ui/core';

import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import TimerIcon from '@material-ui/icons/Timer';
import InfoIcon from '@material-ui/icons/Info';
import InputLabel from '@material-ui/core/InputLabel';

import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';

import Button from '@material-ui/core/Button';

import {
    Link
  } from "react-router-dom";

import { useHistory } from "react-router-dom";

import moment from 'moment';

const diaSemanaIndex = [
    'domingo',
    'segunda',
    'terca-feira',
    'quarta-feira',
    'quinta-feira',
    'Sexta-feira',
    'sabado',
]

Date.prototype.ddmmyyyy = function() {
    var mm = this.getMonth() + 1; // getMonth() is zero-based
    var dd = this.getDate();
    return [(dd>9 ? '' : '0') + dd, '-', (mm>9 ? '' : '0') + mm, '-', this.getFullYear()].join('');
};


const Edicao = ({agendamento, close, editar}) => {

    const [lishorarios, setlisHorarios] = React.useState([])
    const [value, onChange] = React.useState(new Date( agendamento?.dia ));    
    
    const [dia, setDia] = React.useState(new Date( agendamento?.dia ));

    const [diaSelect, setDiaSelect] = React.useState(new Date( agendamento?.dia ) );    
    const classes = useStyles();

    
    React.useEffect( ()=> {
        if( agendamento.servico?.tipo != '3' ){
            initListaHoras( new Date(agendamento?.dia) )
        }
    },[])

    const handleOpen = (date, event) => {
        initListaHoras(date)
    };

    const handleChange = (event) => {
        setDiaSelect(event.target.value);
    };

    const initListaHoras = (dia) => {
        let semana = agendamento.servico.semana.filter( item => item.slug === diaSemanaIndex[dia.getDay()] ? true : false )[0]
        
        let event = dia
        let inicio = semana.inicio.split(":")
        let fim = semana.fim.split(":")

        inicio = event.setHours(inicio[0], inicio[1], '00');
        fim  = event.setHours(fim[0], fim[1], '00');

        let minutos = ( parseInt(agendamento.servico.tempo_atividade.split(":")[0]) * 60 ) + parseInt(agendamento.servico.tempo_atividade.split(":")[1])

        const loop = (date, fim, lista, index, minutos, myloop) => {
            if( date < fim ){
                lista.push(date)
                let novoDia =  moment(date).add(minutos, 'm').toDate();
                index = index + 1;
                return myloop(novoDia, fim, lista, index, minutos, myloop)
            }
            return lista
        }

        let result = loop(inicio, fim, [], 0, minutos, loop)
        setlisHorarios(result)
    }


    return (
        <ContainerModal>
                <CardInfo>
                    <WrapperCardAvatar>
                        <Avatar alt={agendamento.cliente?.first_name} src={agendamento.cliente.cliente?.avatar} style={{ width: 60, height: 60}} />
                    </WrapperCardAvatar>
                    <WrapperCardinfo >
                        <Typography variant="h6">
                            {agendamento.cliente?.first_name}
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                            {agendamento.cliente.cliente?.telefone}
                        </Typography>
                    </WrapperCardinfo>
                    <a target="_blank" href={`http://api.whatsapp.com/send?phone=55${agendamento.cliente.cliente?.telefone}`} >
                        <WrapperCardAction >
                                <WhatsAppIcon />
                        </WrapperCardAction>
                    </a>
                </CardInfo>
                <Divider />
            {
                agendamento.servico?.tipo != '3' 
                    ?
                <>
                <WrapperCalendario>
                    <Calendar
                        className={classes.reactCalendar}
                        onChange={onChange}
                        onClickDay={handleOpen}
                        value={value}
                        tileDisabled={({activeStartDate, date, view }) => (
                            date >= new Date() ? agendamento.servico?.semana.map( semana => diaSemanaIndex.indexOf(semana.slug) ).filter( id => id === date.getDay() ? true : false ).length > 0 ? false : true : date.getDate() >= new Date().getDate() ? false : true 
                        )}
                    />
                </WrapperCalendario>
                <WrapperFormDia >
                    <FormControl variant="outlined" className={classes.formControl}>
                        <Select
                        native
                        value={diaSelect}
                        onChange={handleChange}
                        inputProps={{
                            name: 'dia',
                            id: 'select-dia',
                        }}
                        >
                        {
                            lishorarios.map( (item, index)=> <option value={item}>{ moment(item).format('H:mm') }</option> )
                        }
                        </Select>
                    </FormControl>
                </WrapperFormDia>
                </>
                :
                <></>
            }
            <WrapperButtonOption> 
                    <Button variant="contained" color="secondary" disableElevation style={{ width: '180px', margin: '5px' }} onClick={close} > CANCELAR </Button>
                    <Button variant="contained" color="primary" disableElevation style={{ width: '180px', margin: '5px' }} onClick={(event) => editar(agendamento, diaSelect, close) }  > SALVAR </Button>
            </WrapperButtonOption> 
        </ContainerModal>
    );
}

export  { Edicao };
