import React from 'react';

import {
  WrapperHeader,
  WrapperAdd,
  WrapperFormEndereco,
  WrapperFormEnderecoLine
} from './styles';

import { 
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  ListItemText,
  ListItemAvatar,
  TextField,
  Avatar,
} from '@material-ui/core';


import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import EmailIcon from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';
import RoomIcon from '@material-ui/icons/Room';
import PersonIcon from '@material-ui/icons/Person';
import WorkIcon from '@material-ui/icons/Work';
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import FaceIcon from '@material-ui/icons/Face';

import { APIURL } from '../../environment';

import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';
import { atualiza } from '../../utils/Services';

import SaveIcon from '@material-ui/icons/Save';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import InputMask from 'react-input-mask';

import { Valida } from '../../utils/Validando';

const Perfil = () => {

  const theme = useTheme();
  const [perfil, setPerfil, PerfilInicial, addPerfil, rmPerfil, atuPerfil, upPerfil] = useGenericModel('account_profile');
  const [openALert, closeAlert, Alert] = useSnackbarsAlert()
  const [openBackdro, closeBackdro, Backdro] = useBackdrop()

  const [changeform, setChangeform] = React.useState(false)

  const [form, setInput] = React.useState({
    nome: {value: "", error: false, valido: false},
    profissao: {value: "", error: false, valido: false},
    email: {value: "", error: false, valido: false},
    telefone: {value: "", error: false, valido: false},
    CEP: {value: "", error: false, valido: false},
    localidade: {value: "", error: false, valido: false},
    uf: {value: "", error: false, valido: false},
    endereco: {value: "", error: false, valido: false},
    numero: {value: "", error: false, valido: false},
    complemento: {value: "", error: false, valido: false},
    instagram: {value: "", error: false, valido: false},
    facebook: {value: "", error: false, valido: false},
})

  React.useEffect(() => {
    console.log(perfil)
    if(perfil){
      setInput({
          nome: {value: perfil.user?.first_name ? perfil.user?.first_name : "", error: false, valido: true},
          profissao: {value: perfil?.profissao ?  perfil.profissao : "", error: false, valido: true},
          email: {value: perfil.user?.email ?  perfil.user.email : "", error: false, valido: true},
          telefone: {value: perfil?.telefone ? perfil.telefone: "", error: false, valido: true},
          CEP: {value: perfil?.CEP ? perfil.CEP: "", error: false, valido: true},
          localidade: {value: perfil?.localidade ? perfil.localidade: "", error: false, valido: true},
          uf: {value: perfil?.uf ? perfil.uf: "", error: false, valido: true},
          endereco: {value: perfil?.endereco ? perfil.endereco: "", error: false, valido: true},
          numero: {value: perfil?.numero ? perfil.numero: "", error: false, valido: true},
          complemento: {value: perfil?.complemento ? perfil.complemento: "", error: false, valido: true},
          instagram: {value: perfil?.instagram ? perfil.instagram: "", error: false, valido: true},
          facebook: {value: perfil?.facebook ? perfil.facebook: "", error: false, valido: true},
      })
    }
  },[perfil]);

  const  lidandoForm = (event, validate) => {
    setChangeform(true)
    let valida  = Valida[validate]
    if(valida){
        let result = valida(event.target.value, event.target.name, event.target.required)
        if (result.status){
            setInput({...form, [event.target.name]: {value: result.value, error: false, valido: true}})
        }else{
            setInput({...form, [event.target.name]: {value: result.value, error: result.msg, valido: false}})
        }
    }
}

const lidandoFormCEP = async (event, validate) => {
    setChangeform(true)
    let valida  = Valida[validate]
    setInput({...form, [event.target.name]: {value: event.target.value, error: false, valido: true}})
    if(valida){
        let result = await valida(event.target.value, event.target.name, event.target.required)
        if (result.status){
            setInput({...form,
                      uf: {value: result.dados?.uf, error: false, valido: true},
                      localidade: {value: result.dados?.localidade, error: false, valido: true},
                      endereco: {value: `${result.dados?.logradouro}, ${result.dados?.bairro}`, error: false, valido: true},
                      [event.target.name]: {value: result.value, error: false, valido: true}, 
                    })
        }else{
            setInput({...form, [event.target.name]: {value: result.value, error: result.msg, valido: false}})
        }
    }
}

  async function enviaFormularioProfile(){
    if( Valida.formEhValido(form) ){
        openBackdro()
        let novoPerfil = {...perfil}
        form.avatar?.value ? novoPerfil.avatar = form.avatar?.value: delete novoPerfil.avatar
        delete novoPerfil.user.username
        novoPerfil.user.email = form.email.value.trim()
        novoPerfil.user.first_name = form.nome.value.trim()
        novoPerfil.profissao = form.profissao.value
        novoPerfil.telefone = form.telefone.value
        novoPerfil.CEP = form.CEP.value
        novoPerfil.localidade = form.localidade.value
        novoPerfil.uf = form.uf.value
        novoPerfil.endereco = form.endereco.value
        novoPerfil.numero = form.numero.value
        novoPerfil.complemento = form.complemento.value
        novoPerfil.instagram = form.instagram.value
        novoPerfil.facebook = form.facebook.value
        let result = await atualiza('account_profile', novoPerfil)
        setTimeout(() => {
          if ( result?.id ) {
              setPerfil(result)
              openALert({
                msg: "Perfil atualizado",
                severity: "success"
              })
          }else{
              openALert({
                msg: "Solicitao no pode ser concluida",
                severity: "error"
              })
          }
          closeBackdro()
        }, 300);
    }
}

  return (
    <div>
                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-avatar"
                      >
                        <ListItemAvatar>
                          {
                            perfil?.avatar ? 
                              <Avatar alt={perfil.user?.first_name} src={`${APIURL}${perfil?.avatar}`} style={{ width: 40, height: 40}} />
                            :
                              <FaceIcon />
                          }
                        </ListItemAvatar>
                          <ListItemText primary="Avatar" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                      <input type="file" onChange={ 
                        (event) => {
                          let novoInput = {...form}
                          novoInput['avatar'] = {value: event.target.files[0], error: false, valido: true}
                          setInput(novoInput)
                          setChangeform(true)
                        }
                    } /> 
                    </AccordionDetails>
                </Accordion>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Nome" secondary={form.nome?.value} />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-nome" 
                        label={ form.nome?.value ? false : "Nome" } 
                        variant="outlined" 
                        size="small"
                        name="nome"
                        required
                        value={form.nome?.value}
                        onChange={ (event) => lidandoForm(event, 'text') }
                        helperText={form.nome?.error ? form.nome.error : ""}
                        error={form.nome?.error ? true : false}
                      /> 
                    </AccordionDetails>
                </Accordion>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-profissao"
                      >
                        <ListItemAvatar>
                            <WorkIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Profisso" secondary={form.profissao?.value} />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-profissao" 
                        label={ form.profissao?.value ? false : "Profisso" } 
                        variant="outlined" 
                        size="small"
                        name="profissao"
                        value={form.profissao?.value}
                        onChange={ (event) => lidandoForm(event, 'text') }
                        helperText={form.profissao?.error ? form.profissao.error : ""}
                        error={form.profissao?.error ? true : false}
                        /> 
                    </AccordionDetails>
                </Accordion>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-email"
                      >
                        <ListItemAvatar>
                            <EmailIcon />
                        </ListItemAvatar>
                        <ListItemText primary="E-mail" secondary={ form.email?.value } />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-email"
                        type="email"
                        name="email"
                        required
                        label={ form.email?.value ? false : "E-mail" } 
                        variant="outlined" 
                        size="small" 
                        value={form.email?.value}
                        onChange={ (event) => lidandoForm(event, 'email') }
                        helperText={form.email?.error ? form.email.error : ""}
                        error={form.email?.error ? true : false}                      
                        /> 
                    </AccordionDetails>
                </Accordion>
  
                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-telefone"
                      >
                      <ListItemAvatar>
                          <PhoneIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Telefone" secondary={form.telefone?.value} />
                    </AccordionSummary>
                    <AccordionDetails>
                        <InputMask mask="(99) 99999-9999" 
                          value={form.telefone?.value}
                          onChange={ (event) => lidandoForm(event, 'telefone') }                     
                          >
                            {() => (
                                <TextField
                                    label={ form.telefone?.value ? false : "Telefone" } 
                                    helperText="(11) 99999-9999"
                                    name="telefone"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    size="small"
                                    helperText={form.telefone?.error ? form.telefone.error : ""}
                                    error={form.telefone?.error ? true : false}
                                /> 
                            )}
                        </InputMask>
                    </AccordionDetails>
                </Accordion>              

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-endereco"
                      >
                      <ListItemAvatar>
                          <RoomIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Endereo" secondary={`${form.endereco?.value}, ${form.numero?.value} ${form.complemento?.value} ${form.localidade?.value} - ${form.uf?.value} ${form.CEP?.value}`} />
                    </AccordionSummary>
                    <AccordionDetails>
                      <WrapperFormEndereco>
                      <InputMask mask="99999-999" 
                          value={form.CEP?.value}
                          onChange={ (event) => lidandoFormCEP(event, 'CEP') }                     
                          >
                            {() => (
                          <TextField 
                            fullWidth 
                            id="input-CEP" 
                            label={ form.CEP?.value ? false : "CEP" }
                            variant="outlined" 
                            size="small"
                            name="CEP"
                            helperText={form.CEP?.error ? form?.CEP.error : ""}
                            error={form.CEP?.error ? true : false} 
                            style={{
                              maxWidth: '300px',
                              margin: '0 0 8px 0'
                            }}                        
                            />
                            )}
                        </InputMask>
                          <WrapperFormEnderecoLine >
                              <TextField 
                                fullWidth 
                                id="input-cidade" 
                                label={ form.localidade?.value ? false : "Cidade" }
                                variant="outlined" 
                                size="small"
                                name="localidade"
                                value={form.localidade?.value}
                                onChange={ (event) => lidandoForm(event, 'text') }
                                helperText={form.localidade?.error ? form?.localidade.error : ""}
                                error={form.localidade?.error ? true : false}
                                style={{
                                  maxWidth: '275px',
                                  margin: '0 5px 8px 0'
                                }}                          
                                /> 
                              <TextField 
                                fullWidth 
                                id="input-uf" 
                                label={ form.uf?.value ? false : "Estado" }
                                variant="outlined" 
                                size="small"
                                value={form.uf?.value}
                                onChange={ (event) => lidandoForm(event, 'text') }
                                helperText={form.uf?.error ? form?.uf.error : ""}
                                error={form.uf?.error ? true : false} 
                                style={{
                                  maxWidth: '120px',
                                  margin: '0 0 8px 0'
                                }}                          
                                />
                          </WrapperFormEnderecoLine>                           
                          <WrapperFormEnderecoLine >
                              <TextField 
                                fullWidth 
                                id="input-endereco" 
                                label={ form.endereco?.value ? false : "Endereo" }
                                variant="outlined" 
                                size="small"
                                name="endereco"
                                value={form.endereco?.value}
                                onChange={ (event) => lidandoForm(event, 'text') }
                                helperText={form.endereco?.error ? form?.endereco.error : ""}
                                error={form.endereco?.error ? true : false}
                                style={{
                                  maxWidth: '295px',
                                  margin: '0 5px 8px 0'
                                }}                          
                                /> 
                              <TextField 
                                fullWidth 
                                id="input-numero" 
                                label={ form.numero?.value ? false : "Numero" }
                                variant="outlined" 
                                size="small"
                                type="number"
                                name="numero"
                                value={form.numero?.value}
                                onChange={ (event) => lidandoForm(event, 'numero') }
                                helperText={form.numero?.error ? form?.numero.error : ""}
                                error={form.numero?.error ? true : false} 
                                style={{
                                  maxWidth: '100px',
                                  margin: '0 0 8px 0'
                                }}                          
                                />
                          </WrapperFormEnderecoLine>                           
                          <TextField 
                            fullWidth 
                            id="input-complemento" 
                            label={ form.complemento?.value ? false : "Complemento" }
                            variant="outlined" 
                            size="small"
                            name="complemento"
                            value={form.complemento?.value}
                            onChange={ (event) => lidandoForm(event, 'text') }
                            helperText={form.complemento?.error ? form?.complemento.error : ""}
                            error={form.complemento?.error ? true : false}
                            style={{
                              maxWidth: '400px',
                              margin: '0 0 8px 0'
                            }}                         
                            /> 
                      </WrapperFormEndereco> 
                    </AccordionDetails>
                </Accordion>                

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-instagram"
                      >
                      <ListItemAvatar>
                          <InstagramIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Instagram" secondary={ form.instagram?.value } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                        fullWidth 
                        id="input-instagram" 
                        label={ form.instagram?.value ? false : "Instagram" }  
                        variant="outlined" 
                        size="small"
                        name="instagram"
                        value={form.instagram?.value}
                        onChange={ (event) => lidandoForm(event, 'text') }
                        helperText={form.instagram?.error ? form?.instagram.error : ""}
                        error={form.instagram?.error ? true : false}                  
                      /> 
                    </AccordionDetails>
                </Accordion> 

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-facebook"
                      >
                      <ListItemAvatar>
                          <FacebookIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Facebook" secondary={ form.facebook?.value } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                        fullWidth 
                        id="input-facebook" 
                        label={ form.facebook?.value ? false : "Facebook" } 
                        variant="outlined" 
                        size="small" 
                        name="facebook"
                        value={form.facebook?.value}
                        onChange={ (event) => lidandoForm(event, 'text') }
                        helperText={form.facebook?.error ? form.facebook.error : ""}
                        error={form.facebook?.error ? true : false}                            
                        /> 
                    </AccordionDetails>
                </Accordion> 
                <Alert />
                <Backdro />

          <Zoom
              key="primary"
              in={1}
              timeout={{
                enter: theme.transitions.duration.enteringScreen,
                exit: theme.transitions.duration.leavingScreen,
              }}
              style={{
                transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
              }}
              unmountOnExit
            >
              <Fab 
                onClick={ Valida.formEhValido(form) && changeform ? enviaFormularioProfile : () => {} }
                aria-label="addicon"
                disabled={ Valida.formEhValido(form) && changeform ? false : true } 
                color="primary"
                style={{
                  position: 'fixed',
                  bottom: '30px',
                  right: '30px',
                  backgroundImage: Valida.formEhValido(form) && changeform ? 'linear-gradient(to right, #8f00dd , #6866ff)' : 'none',
                }}
              >
                <SaveIcon />
              </Fab>
          </Zoom>
    </div>

  );

}

export { Perfil }