import React from 'react';

import { 
    Container,
    EditingArea,
} from './styles';

import SaveIcon from '@material-ui/icons/Save';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';

import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';
import ReactQuill from 'react-quill';

import { Valida } from '../../utils/Validando';
import { atualiza } from '../../utils/Services';

const Bio = () => {

    const theme = useTheme();
    const [perfil, setPerfil, PerfilInicial, addPerfil, rmPerfil, atuPerfil, upPerfil] = useGenericModel('account_profile');
    const [openALert, closeAlert, Alert] = useSnackbarsAlert()
    const [openBackdro, closeBackdro, Backdro] = useBackdrop()
    
    const [changeform, setChangeform] = React.useState(false)

    const [form, setInput] = React.useState({
        bio: {value: "", error: false, valido: false},
    })

    React.useEffect(() => {
        if(perfil){
          setInput({
            bio: {value: perfil?.bio ? perfil.bio : "", error: false, valido: true},
          })
        }
      },[perfil]);


    const lidandoForm = (value, validate) => {
        setChangeform(true)
        let valida  = Valida[validate]
        if(valida){
            let result = valida(value)
            if (result.status){
                setInput({ bio: {value: result.value, error: false, valido: true} })
            }else{
                setInput({ bio: {value: result.value, error: result.msg, valido: false}})
            }
        }
    }      

    async function enviaFormularioProfile(){
        if( Valida.formEhValido(form) ){
            openBackdro()
            let novoPerfil = {...perfil}
            delete novoPerfil.avatar
            delete novoPerfil.user.username
            novoPerfil.bio = form.bio.value

            let result = await atualiza('account_profile', novoPerfil)
            setTimeout(() => {
              if ( result?.id ) {
                  setPerfil(result)
                  openALert({
                    msg: "Biografia atualizada",
                    severity: "success"
                  })
              }else{
                  openALert({
                    msg: "Solicitao no pode ser concluida",
                    severity: "error"
                  })
              }
              closeBackdro()
              setChangeform(false)
            }, 300);
        }
    }


    return (
        <Container>
            <ReactQuill theme="snow" value={form.bio.value} 
                onChange={(value) => lidandoForm(value, 'text')} 
                style={{
                width: '100%',
                heigth: '100%',
            }} />
            <Alert />
            <Backdro />

            <Zoom
              key="primary"
              in={1}
              timeout={{
                enter: theme.transitions.duration.enteringScreen,
                exit: theme.transitions.duration.leavingScreen,
              }}
              style={{
                transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
              }}
              unmountOnExit
            >
              <Fab 
                onClick={ Valida.formEhValido(form) && changeform ? enviaFormularioProfile : () => {} }
                aria-label="addicon"
                disabled={ Valida.formEhValido(form) && changeform ? false : true } 
                color="primary"
                style={{
                  position: 'fixed',
                  bottom: '30px',
                  right: '30px',
                  backgroundImage: Valida.formEhValido(form) && changeform ? 'linear-gradient(to right, #8f00dd , #6866ff)' : 'none',
                }}
              >
                <SaveIcon />
              </Fab>
          </Zoom>


        </Container>
    );

}

export  { Bio };
