import React from 'react';
import TextField from '@material-ui/core/TextField';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';

import {
    WrapperUpload,
    ButtonUploadBox,
    WrapperModal,
    WrapperForm,
    WrapperOption,
    WrapperOptionButton
} from './styles';

import Button from '@material-ui/core/Button';

import { Valida } from '../../utils/Validando';
import { envia } from '../../utils/Services';

const AdicionarGaleria = ({close, Backdro, openALert}) => {

    const hiddenFileInput = React.useRef(null);
    const [file, setFile] = React.useState(null);

    const [img, setImg] = React.useState(null);
    const [openBackdro, closeBackdro] = Backdro

    const [form, setInput] = React.useState({
        nome: {value: "", error: false, valido: false},
        descricao: {value: "", error: false, valido: false},
    })

    const handleClick = event => {
      hiddenFileInput.current.click();
    };

    const handleChangeFile = (event) => {
        if(event.target.files[0]){
            setFile(URL.createObjectURL(event.target.files[0]))
        }
        setImg(event.target.files[0])
    }

    React.useEffect( ()=>{},[])

    const lidandoForm = (event, validate) => {
        let valida  = Valida[validate]
        if(valida){
            let result = valida(event.target.value, event.target.name, event.target.required)
            if (result.status){
                setInput({...form, [event.target.name]: {value: result.value, error: false, valido: true}})
            }else{
                setInput({...form, [event.target.name]: {value: result.value, error: result.msg, valido: false}})
            }
        }
    }

    async function enviarFormulario(){
        openBackdro()
        close()
        let novaImagem = {
          nome: form.nome?.value,
          descricao: form.descricao?.value,
        }
        if(img){
            novaImagem['img'] = img
        }
        let result = await envia('account_image',novaImagem)
        setTimeout(() => {
            if ( result?.id ) {
                openALert({
                  msg: "Imagem adicionada",
                  severity: "success"
                })
            }else{
                openALert({
                  msg: "Solicitao no pode ser concluida",
                  severity: "error"
                })
            }
            closeBackdro()
        }, 300);
    }

    return (
        <WrapperModal>
            <WrapperForm>
                    <TextField
                    id="descricao-nome"
                    label="Nome"
                    style={{ margin: '5px 0 10px 0' }}
                    placeholder=""
                    fullWidth
                    required
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                        shrink: true,
                    }}
                    name="nome"
                    value={form.nome?.value}
                    onChange={ (event) => lidandoForm(event, 'text') }
                    helperText={form.nome?.error ? form.nome.error : "Nome da imagem"}
                    error={form.nome?.error ? true : false}
                    />
                    <TextField
                    id="descricao-id"
                    label="Descrio"
                    style={{ margin:  '5px 0 0 0' }}
                    multiline
                    fullWidth
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                        shrink: true,
                    }}
                    name="descricao"
                    required
                    value={form.descricao?.value}
                    onChange={ (event) => lidandoForm(event, 'text') }
                    helperText={form.descricao?.error ? form.descricao.error : "Descrio da imagem"}
                    error={form.descricao?.error ? true : false}
                />

                <FormControl>
                    <WrapperUpload>
                        <ButtonUploadBox onClick={handleClick} src={file} >
                            { file ? <></> :  <AddPhotoAlternateIcon /> }
                        </ButtonUploadBox>
                        <input type="file"
                                onChange={handleChangeFile}
                                ref={hiddenFileInput}
                                style={{display:'none'}} 
                        /> 
                    </WrapperUpload> 
                    <FormHelperText>Imagem de referncia</FormHelperText>
                </FormControl>
            </WrapperForm>
            <WrapperOption>
                <WrapperOptionButton>
                    <Button onClick={() => close() } variant="contained" color="secondary" style={{ width: '100%' }} >
                        CANCELAR
                    </Button>
                </WrapperOptionButton>
                <WrapperOptionButton>
                    <Button onClick={() => enviarFormulario() } variant="contained" color="primary" style={{ width: '100%' }} >
                        ADICIONAR
                    </Button>
                </WrapperOptionButton>
            </WrapperOption>
            

        </WrapperModal>
    )

}

export { AdicionarGaleria } 